import React from 'react';
import InpatientReportsByMonthContainer from "../containers/InpatientReportsByMonthContainer";
import {useParams} from "react-router-dom"

const InpatientReportsByMonthPage = () => {
    const {year, month} = useParams()
    return (
        <>
            <InpatientReportsByMonthContainer year={year} month={month}/>
        </>
    );
};

export default InpatientReportsByMonthPage;